
import { defineComponent } from "@vue/runtime-core";

    export default defineComponent({
        name: "CreationPopUp",
        methods: {
            add(){
                this.$emit('add-assignment', this.inputData);
            },
            cancel(){
                this.$emit('cancel-pop-up');
            }
        },
        data() {
            return {
                inputData: {
                    name: '',
                    date: '',
                    subject: '',
                    done: false,
                }
            }
        }
    })
